import { Component, ViewChild, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { ProductService } from "../../providers/product.services";
import { ModalController } from '@ionic/angular';
import { AddAddressPage } from '../address/modal/addAddress';
import { EditAddressPage } from './modal/editAddress';
import { UserData } from '../../providers/user-data';
import { ToastController } from '@ionic/angular';
import { AlertController } from '@ionic/angular';
import { environment } from "../../../environments/environment";

@Component({
  selector: 'page-manage-address',
  templateUrl: 'manage-address.html',
  styleUrls: ['./manage-address.scss'],
})
export class ManageAddressPage implements OnInit {


  public addressList: any=[];
  public cartId: any
  public enableCheckoutScreen: boolean = false
  constructor(
    private _ProductService: ProductService, public modalController: ModalController,
    private _Router: ActivatedRoute, public Router: Router,
    private _userData: UserData, private toastController: ToastController,
    public alertController:AlertController
  ) { }




  public isSearchEnable:Boolean=false;
public baseUrl = environment.baseUrl + '/';
public searchproductList:any=[]
search(query) {
  if (query.target.value.length >= 2) {
    this.isSearchEnable = true
    this._ProductService.search({
      search: query.target.value,
      filter: 'Product'
    },localStorage.getItem('shopID')).subscribe((data: any) => {
      console.log(data, "data")
      this.searchproductList = data.productList;
    });
  }

  if (query.target.value.length == 0) {
    this.isSearchEnable = false
    // this.getAnalyticalData()
  }
}
   ngOnInit() {
    this.getAddress();
    this.getCartCount();
    this.cartId = this._Router.snapshot.params.cartId;
    console.log(this.cartId, "cartId")
  }

  public showSearchBox: Boolean = false;
  enableSearch() {
    this.showSearchBox = true
  }

  backToNormal(){
    this.showSearchBox = false
  }

  getAddress() {
    this._ProductService.getAddress(localStorage.getItem('userId')).subscribe((data: any) => {
      this.addressList = data;
      console.log(this.addressList)
    });
  }

  async deleteConfirm(addressId) {
    const alert = await this.alertController.create({
      header: 'Confirm!',
      message: 'Are You Sure You Want To delete ?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
          text: 'Delete',
          handler: () => {
            this.deleteAddress(addressId)
          }
        }
      ]
    });
    await alert.present();
  }

  async openAddAdressModal() {
    const modal = await this.modalController.create({
      component: EditAddressPage
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null) {
        this.getAddress()
      }
    });

    return await modal.present();
  }



  async openEditAdressModal(_addressData) {
    const modal = await this.modalController.create({
      component: EditAddressPage,
      componentProps: {
        addressData: _addressData
      }
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned !== null) {
        this.getAddress()
      }
    });

    return await modal.present();
  }


  public selectedEntry: any = {
    _id: ""
  }
  onSelectionChange(entry) {
    this.selectedEntry = Object.assign({}, this.selectedEntry, entry);
    console.log(this.selectedEntry, "select")
    if (this.selectedEntry) {
      this.enableCheckoutScreen = true
    }
  }

  processedToCheckOutPage() {
    this.Router.navigateByUrl(`/checkout/${this.cartId}/${this.selectedEntry._id}`);
  }

  deleteAddress(addressId) {
    this._userData.deleteAddress(addressId).subscribe(async (data: any) => {
      this.getAddress();
      const toast = await this.toastController.create({
        message: 'Address Deleted',
         duration: 3000,
                color:'secondary',
        position: 'bottom',
        animated: true,
      });
      toast.present();
    });
  }

  public cartCount =0
  getCartCount(){
    let shopId = localStorage.getItem('shopID')
    var arr = [];
    this._ProductService
      .cartList(localStorage.getItem("userId"),shopId)
      .subscribe((data: any) => {
        if (data.length !== 0) {
          if(data.length){
            this.cartCount = data[0].productDetails.length;
          }

        }
      })
  }
}
